import React from 'react';
import Card from './components/Card';
import Scoreboard from './components/Scoreboard';
import Container from './components/Container'
import Wrapper from './components/Wrapper';
import gameImages from './gameImages.json';
import './App.css';

class App extends React.Component {
  state = {
    currentScore: 0,
    highScore: 0,
    gameImages
  }

  checkClick = (clicked, id) => {
    const newHighScore = (a, b) => {
      if (a === b)
        return a + 1
      else
        return a
    }


    const shuffle = gameImages => {
      return gameImages.map(a => [Math.random(), a])
        .sort((a, b) => a[0] - b[0])
        .map(a => a[1]);

    }
    const updateState = (currentScore, highScore, gameImages) => {
      this.setState({
        currentScore: currentScore,
        highScore: highScore,
        gameImages: shuffle(gameImages)
      })
    }
    if (clicked) {
      const gameImages = this.state.gameImages.map(image => {
        return {
          image_id: image.image_id,
          image_src: image.image_src,
          clicked: false
        }
      })

      updateState(0,
        this.state.highScore,
        shuffle(gameImages)
      );


    } else {

      const gameImages = this.state.gameImages.map(image => {
        if (image.image_id !== id) {
          return {
            image_id: image.image_id,
            image_src: image.image_src,
            clicked: image.clicked
          }
        } else return {
          image_id: image.image_id,
          image_src: image.image_src,
          guessed: true
        }
      });

      updateState(
        (this.state.currentScore + 1),
        newHighScore(this.state.highScore,
          this.state.currentScore),
        shuffle(gameImages)
      );
    };
  };


  render() {
    return (
      <Wrapper>
        <Scoreboard currentScore={this.state.currentScore}
          highScore={this.state.highScore} />
        <Container>
          {this.state.gameImages.map(image => (
            <Card id={image.image_id}
              image={image.image_src}
              clicked={image.clicked}
              checkClick={this.checkClick}
            />
          ))}
        </Container>
      </Wrapper>
    )
  }
}

export default App;
